.wrapper{
  background-color: #121127;
  padding: 15px 0 20px 0;
  color: white;
  min-height: 20vh;

  .onlineJbBlock{
    display: flex;
    min-height: 25vh;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    border-top: 2px solid #2e2d71;
    border-bottom: 2px solid #2e2d71;
    padding: 10px 0 10px 0;
  }
  .title{
    text-align: center;
    font-size: 25px;
  }
  .buttonWrapper {
    vertical-align: baseline;
    text-align: center;
    button {
      background: #fe3e57;
      color: #fff;
      border: 1px solid #fe3e57;
      margin-top: 20px;
      border-radius: 40px;
      font-size: .9375rem;
      padding: 12px 25px 9px;
      font-weight: 600;
      position: relative;
    }
  }
}
